<template>
    <!--  <signature :src="'./signature.svg'"></signature> -->
    <div ref="item" class="signature" v-html="svg"></div>
</template>

<script>
    export default {
        props: ["src"],
        data() {
            return {
                svg: "",
            };
        },
        async mounted() {
            this.svg = await fetch(this.src).then((res) => res.text());
        },
        updated() {
            let that = this;
            this.$anime({
                targets: this.$refs.item.querySelectorAll("path"),
                strokeDashoffset: [this.$anime.setDashoffset, 0],
                easing: "easeInOutSine",
                duration: 20,
                delay: function(el, i) {
                    return i * 20;
                },
                direction: "alternate",
                loop: false,
                complete: (anim) => {
                    that.$emit("animationend");
                },
            });
        },
    };
</script>
<style lang="less" scoped></style>
