<template>
    <div class="home">
        <loadingAnimation ref="loading" @animationend="loaded" v-show="loadingVisiable" :percentage="percentage" clas="mask"></loadingAnimation>
        <router-view></router-view>
    </div>
</template>

<script>
    import loadingAnimation from "./loadingAnimation.vue";
    export default {
        name: "Home",
        props: [],
        components: { loadingAnimation },
        data() {
            return {
                animate: false,
                loadingAnimateEnd: false,
                percentage: 1,
                loadingVisiable: true, // 这是开场动画的开关
            };
        },
        methods: {
            loaded() {
                this.$refs.loading.$el.classList.add("animate__animated", "animate__fadeOut", "animate__delay-1s");
                this.percentage = 100;
                this.$refs.loading.$el.addEventListener(
                    "animationend",
                    function() {
                        this.$refs.loading.$el.classList.remove("animate__animated", "animate__fadeOut", "animate__delay-1s");
                        this.loadingVisiable = false;
                    }.bind(this)
                );
                this.loadingAnimateEnd = true;
            },
        },
    };
</script>
<style lang="less" scoped>
    .home {
        height: 100%;
        width: 100vw;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .fiveRound {
            max-width: 80%;
        }
    }
</style>
